import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,ViewController,App } from 'ionic-angular';

/**
 * Generated class for the MenuIpcContactPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-menu-ipc-contact',
  templateUrl: 'menu-ipc-contact.html',
})
export class MenuIpcContactPage {
  IPCContactUserData:any;
  badges:any;  

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl:ViewController,
    public app:App
  ) {
    this.IPCContactUserData = navParams.get('IPCContactUserData');
    this.badges = navParams.get('badges');
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuIpcContactPage');
    console.log(this.IPCContactUserData);
  }

  goToPage(page) {    

    this.viewCtrl.dismiss().then(() => {
      this.app.getRootNav().push(page, {
        //p2buser: this.p2buser,         
      });
    });
  }

  isEmptyObject(obj) {
    for(var prop in obj) {
       if (obj.hasOwnProperty(prop)) {
          return false;
       }
    }  
    return true;
  }

  parse(val) {
    var intValue = parseInt(val);

    if(intValue > 0) {
        return true;
    } else {
        return false;
    }
  }

}



